.portfolio {
    margin-top: 0;
    text-align: center;
}

.landing_text {
    position: absolute;
    left: var(--portfolio-left);
    top: var(--portfolio-top);
    padding-left: 5vw;
    color: lightyellow;
}

.projects_div {
    /* border: 1px solid black; */
    margin: 5vh auto;
    display: block;
    width: 70vw;
}

.project_card {
    /* border: 1px solid black; */
    /* width: 250px; */
    padding: 5vw;
    display: flex;
    justify-content: space-around;
    margin-bottom: 50px;
    background-color:rgb(240,249,225);
    box-shadow: 5px -5px 4px #888888;
    border-radius: 8px;
    position: relative;
    left: 0;
    bottom: 0;
}

.project_card:hover {
    /* background-color: rgb(240,255,225); */
    position: relative;
    left: -5px;
    bottom: -5px;
    box-shadow: 15px -15px 15px #888888;
    cursor: pointer;
    transition: all 0.3s;
}

.project_card_left_div {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.project_img {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    /* border: 1px solid gray; */
    /* margin: 0 auto; */
    overflow: hidden;
}

.project_card_right_div {
    text-align: left;
    padding-left: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.project_description {
}

@media only screen and (max-width: 600px) {
    .project_card {
        display: block;
    }
    .project_card_right_div {
        padding-left: 0;
    }
}



