.profile_div {
    background-color: rgba(191, 201, 157, 0.8);
    height: 100%;
    width: 60%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    box-sizing: border-box;
    border: 2px solid rgb(187, 197, 154);
    
}

.contact_div {
    background-color:  rgba(112, 146, 113, 0.6);
    height: 40%;
    box-sizing: border-box;
    border: solid 3px rgb(149, 194, 151);
}


.skills_div {
    background-color: rgba(140, 115, 150, 0.6);
    width: 30%;
    z-index: 0;
    transition: 0.2s;
    box-sizing: border-box;
    border: 3px solid rgb(185, 151, 197);
}

.skills_div:hover {
    background-color: rgba(185, 151, 197, 0.9);
    cursor: pointer;
    border: outset 3px rgb(185, 151, 197);
}

.skills_page {
    background-color: rgb(185, 151, 197);
    height: 100vh;
    width: 100vw;
    position: absolute;
    left: calc(0.5*(50vh + 50vw * var(--sqrt3) - 100vw));
    top: calc(0.5*(50vw + 50vh * var(--sqrt3)  - 100vh));
    transform: rotate(30deg);
    z-index: 100;
    transition: 0.3s;
    overflow: auto;
}
/* Portfolio */

.portfolio_div {
    background-color:  rgba(112, 146, 113, 0.6);
    height: 27%;
    width: 100%;
    z-index: 0;
    transition: 0.2s;
    box-sizing: border-box;
    border: solid 3px rgb(149, 194, 151);
}

.portfolio_div:hover {
    background-color: rgba(149, 194, 151, 0.9);
    cursor: pointer;
    border: outset 3px rgb(149, 194, 151);
}

.portfolio_page {
    background-color: rgb(149, 194, 151);
    height: 100vh;
    width: 100vw;
    position: absolute;
    left: calc(0.5*(50vh + 50vw * var(--sqrt3) - 100vw));
    top: calc(0.5*(50vw + 50vh * var(--sqrt3) - 100vh));
    transform: rotate(30deg);
    z-index: 100;
    transition: 0.3s;
    overflow: auto;
}

/* Resume */

.resume_div {
    background-color:rgba(126, 155, 163, 0.6);
    width: 40%;
    z-index: 0;
    transition: 0.2s;
    box-sizing: border-box;
    border: solid 3px rgb(149,184,194);
}

.resume_div:hover {
    background-color: rgba(149,184,194, 0.9);
    cursor: pointer;
    border: outset 3px rgb(149,184,194);
}

.resume_page {
    background-color: rgb(149, 184, 194);
    height: 100vh;
    width: 100vw;
    position: absolute;
    left: calc(0.5*(50vh + 50vw * var(--sqrt3) - 100vw));
    top: calc(0.5*(50vw + 50vh * var(--sqrt3) - 100vh));
    transform: rotate(30deg);
    z-index: 100;
    transition: 0.3s;
    overflow: auto;
}

.background_img {
    width: 100vw;
    height: 100vh;
}

.page {
    position: relative;
    overflow: hidden;
    width: 100vw;
    height: 100vh;
}

.all_divs {
    position: absolute;
    width: var(--external-width);
    height: var(--external-height);
    box-sizing: border-box;
    left: 0;
    top: 0; 
    transform: translate(var(--external-left), var(--external-top)) translateZ(0) rotate(-30deg);
    /* -webkit-transform: translate(var(--external-left), var(--external-top)) translateZ(0) rotate(-30deg); */
}

.second_row_divs {
    display: flex;
    width: 100%;
    height: 73%;
}

.second_row_right_divs {
    width: 70%;
}

.profile_and_resume_div {
    display: flex;
    height: 60%;
}