body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --sqrt3: 1.7320508075688772935274463;
  --external-height: calc(50vw + 50vh * var(--sqrt3));
  --external-width: calc(50vh + 50vw * var(--sqrt3));
  --external-left: calc(-0.5*(var(--external-width) - 100vw));
  --external-top: calc(-0.5*(var(--external-height) - 100vh));
  --skills-top: calc((100vh - 0.27 * var(--external-height) / var(--sqrt3) * 2) * 0.5 * 0.5 * var(--sqrt3) + 0.27 * var(--external-height));
  --skills-left: calc((100vh - 0.27 * var(--external-height) / var(--sqrt3) * 2) / 4);
  --portfolio-top: calc(0.27 * var(--external-height) * 0.5 - 3vh);
  --portfolio-left: calc((100vh - var(--portfolio-top) / var(--sqrt3) * 2) * 0.5);
  --resume-top: calc(50vw + (100vh - 0.73 * 0.4 * var(--external-height) / var(--sqrt3) * 2) / 5 * 0.5 * var(--sqrt3));
  --resume-right: calc((100vh - 0.73 * 0.4 * var(--external-height) / var(--sqrt3) * 2) / 5 * 0.5);
  --contact-bottom: calc(0.73 * 0.4 / 3 * var(--external-height));
  --contact-right: calc(0.5 * (100vh - 0.73 * 0.4 * var(--external-height) / var(--sqrt3) * 2 / 3));
}
