.resume {
    margin-top: 0;
    text-align: center;
}

.resume_text {
    margin: 50px 10vw;

}

.download_div {
    margin: 50px 10vw;
    text-align: right;
}

.download_span {
    text-decoration: none;
    color: yellowgreen;
}

.download_text {
    font-size: large;
}

.download_button {
    background-color: grey;
    border: none;
    border-radius: 5px;
    width: 200px;
    height: 50px;
}

.download_button:hover {
    background-color: indigo;
    cursor: pointer;
}

.download_icon {
    margin-right: 10px;
}

.landing_text {
    position: absolute;
    right: var(--resume-right);
    top: var(--resume-top);
    padding-right: 12vw;
    color: lightyellow;
    backface-visibility: hidden;
}