.skills {
    margin-top: 0;
    text-align: center;
}

.landing_text {
    position: absolute;
    left: var(--skills-left);
    top: var(--skills-top);   
    padding-left: 5vw;
    color: lightyellow;
}

.list_div {
    text-align: center;
}

.list {
    list-style-type: none;
    text-align: center;
    display: table;
    margin: 0 auto;
    padding-left: 0;
}

.skill_text {
    display: inline-block;
    width: 150px;
    padding-top: 20px;
}

.bar {
    background: rgb(231, 174, 174);
    display: inline-block;
    height: 20px;
    width: 70vw;
    border-radius: 10px;
    box-shadow: 0 0 10px;
    overflow: hidden;
}

.bar span {
    height: 20px;
    float: left;
    background: rgb(221, 116, 116);
    border-radius: 10px;
    text-align: right;
    padding-right: 5px;
}

.html {
    width: 85%;
    animation: html 2s;
}

@keyframes html {
    0% {
        width: 0%;
    }
    100% {
        width: 85%;
    }
}

.css {
    width: 80%;
    animation: css 2s;
}

@keyframes css {
    0% {
        width: 0%;
    }
    100% {
        width: 80%;
    }
}

.javascript {
    width: 90%;
    animation: javascript 2s;
}

@keyframes javascript {
    0% {
        width: 0%;
    }
    100% {
        width: 90%;
    }
}

.react {
    width: 70%;
    animation: react 2s;
}

@keyframes react {
    0% {
        width: 0%;
    }
    100% {
        width: 70%;
    }
}

/* tag cloud */
.app_outer {
	align-items: center; 
	bottom: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 100px 15vw;
	position: relative; 
    height: 50vh;
    max-height: 500px;
}

.app_inner {
	display: flex;
	flex-direction: column;
	height: 100%;
	max-width: 1000px;
    width: 100%;

}

.tag_cloud {
	flex: 1;
}

.tag_cloud > div {
	transition: 1.4s;
}

