.profile {
    text-align: center;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    transform: rotate(30deg);
}

.profile_photo {
    border-radius: 50%;
    border: 1px solid black;
    width: 150px;
    height: 150px;
    margin: 20px auto;
}

.name {
    font-weight: 800;
    font-size: 50px;
    margin: 0 auto;
}

.profile_text {
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
}


